 "use client";

import Navbar from "@/components/Navbar";
import { useState } from "react";
import Profile from "@/components/Profile";
import Footer from "@/components/Footer";
import { useRouter } from "next/navigation";

export default function Home() {
  const [showProfile, setShowProfile] = useState(false);
  const router = useRouter();

  return (
    <>
      {showProfile && <Profile setShowProfile={setShowProfile} />}
      <Navbar showProfile={showProfile} setShowProfile={setShowProfile} />

      <main className="p-4">
        <div className="space-x-4">
          <button onClick={() => router.push("/jobs")} className="p-2 bg-blue-500 text-white rounded">
            View Jobs
          </button>
          <button onClick={() => router.push("/internships")} className="p-2 bg-green-500 text-white rounded">
            View Internships
          </button>
          <button onClick={() => router.push("/mentorship")} className="p-2 bg-purple-500 text-white rounded">
            View Mentorship
          </button>
        </div>
      </main>

      <Footer />
    </>
  );
}
